.banner {
    position: relative;
    color: #fff;
    background: linear-gradient(to bottom,#7a19f1,#332179);
    background-attachment: fixed;
    padding: 60px 0px ;

    &:after {
        content: " ";
        right: 0;
        bottom: 0;
        opacity: 1;
        left: 0;
        height: 110px;
        /* width: 100%; */
        position: absolute;
        z-index: 0;
        background-position: center top;
        background-position-x: 50%;
        background-size: 150%;
        background-image: url(data:image/svg+xml;base64,PHN2ZyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnIHZpZXdCb3g9JzAgMCAxNjc5IDE5OC42Jz48cGF0aCBkPSdNODM5LjUgOTYuN0M0ODUuMyA5Ni43IDE3NC41IDU3LjkgMCAwdjE5OC42aDE2NzlWMGMtMTc0LjUgCTU3LjktNDg1LjMgOTYuNy04MzkuNSA5Ni43eicgZmlsbD0nI2ZmZicvPjwvc3ZnPg==);
    }

    .content {
        display: flex;
        justify-content: space-around;
        align-items: center;
        position: relative;
        padding: 0px 50px;
        
        &::after {
            content: " ";
            right: 0;
            bottom: -30%;
            opacity: 0.1;
            left: 0;
            height: 100%;
            width: 100%;
            position: absolute;
            z-index: 0;
            background-position: center;
            background-size: cover;
            background-image: url(data:image/svg+xml;base64,);
        }
        .profilePic  {
            img {
                max-width: 350px;
            }
        }
        .decription {
            padding: 0px 50px;
            text-align: center;

            .designation {
                font-size: 2rem;
            }
        }
    }
}