footer {
    padding: 30px 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    width: 100%;
    background: linear-gradient(45deg, #3c018a, #6400e7);
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.45);

    .contactInfo {
        display: flex;
        align-items: center;
        .subTTl {
            color: rgb(255, 226, 97);
            margin: 0px;
        }
        a {
            align-items: center;
            display: flex;
            margin: 0px 20px;
            color: #fff;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
            }

            i {
                position: relative;
                top: 3px;
                margin-right: 5px;
            }
            img {
                max-width: 20px;
                margin-right: 5px;
            }
        }
    }

    .socialInfo {
        display: flex;
        align-items: center;
        .subTTl {
            color: rgb(255, 226, 97);
            margin: 0px;
        }
        a {
            position: relative;
            top: 0px;
            transform: scale(1);
            align-items: center;
            display: flex;
            margin: 0px 20px;
            color: #fff;
            text-decoration: none;
            transition: all 0.3s ease;
            &:hover {
                top: -3px;
                transform: scale(1.1);
            }

            img {
                width: 20px;
                margin-right: 5px;
            }
        }
    }
}